<script>


	let wsObj;
	let wsUrl = 'wss://www.gasnow.org/ws';

	let rapid = 0;
	let fast = 0;
	let standard = 0;
	let slow = 0;
	let el;
	let gasload = false;

	let setFirstGas = false;
	let setFirstPrice = false;

	let updatePageGasPriceData = (data) => {
		console.log(data.gasPrices);
		if(!gasload) {
			gasload = true;
			el = document.getElementById("rapid");
			el.classList.remove("is-loading");
			el = document.getElementById("fast");
			el.classList.remove("is-loading");
			el = document.getElementById("standard");
			el.classList.remove("is-loading");
			el = document.getElementById("slow");
			el.classList.remove("is-loading");
		}

		if (data && data.gasPrices) {
			rapid = Math.floor(data.gasPrices.rapid / 1e9) || 0;
			fast = Math.floor(data.gasPrices.fast / 1e9) || 0;
			standard = Math.floor(data.gasPrices.standard / 1e9) || 0;
			slow = Math.floor(data.gasPrices.slow / 1e9) || 0;

			if (!setFirstGas) {
				setFirstGas = true;
				gas_price = fast;
			}
		}
	};

	wsObj = new WebSocket(wsUrl);
	wsObj.onopen = (evt) => {
		console.log("Connection open ...");
	};

	wsObj.onmessage = (evt) => {
		const dataStr = evt.data;
		const data = JSON.parse(dataStr);

		if (data.type) {
			updatePageGasPriceData(data.data)
		}
	};

	wsObj.onclose = (evt) => {
		console.log("Connection closed.");
	};

	//////////////eth price//////////////////////

	let ethprice = 0;
	var element;
	let ethpriceremoved = false;
	function reqListener() {
		if(!ethpriceremoved) {
			element = document.getElementById("ethprice");
			element.classList.remove("is-loading");
		}
		ethprice = JSON.parse(this.responseText).ethereum.usd ?? 0;

		if (!setFirstPrice) {
			setFirstPrice = true;
			ethpriceinput = ethprice;
		}

		console.log(ethprice);
	}

	function get_ethprice() {
		var oReq = new XMLHttpRequest();
		oReq.addEventListener("load", reqListener);
		oReq.open("GET", "https://api.coingecko.com/api/v3/simple/price?ids=ethereum&vs_currencies=usd");
		oReq.send();
	}
	get_ethprice();
	var ethinterval = window.setInterval(function () {
		get_ethprice();
	}, 10000);

	let r = [];
	for(let i = 0; i < 24; i++) {
		r.push([]);
		for (let j = 0; j < 7; j++) {
			r[i].push('')
		}
	}
	let days = [];
	let currentTime = new Date();
	let currentDay = currentTime.getDate();
	for (let j = 0; j < 7; j++){
		let jDaysAgo = new Date();
		jDaysAgo.setDate(jDaysAgo.getDate() - j);
		days.push(jDaysAgo.toLocaleString('default', { month: 'long' }) + " " + jDaysAgo.getDate());
	}

	function foo(value) {
		console.log(value);
		gas_price = value;
	}

	function hourlyGasListener() {
		let result = JSON.parse(this.responseText);
		let data = result.data;

		let currentTime = new Date();
		let currentDay = currentTime.getDate();

		let currentHour = currentTime.getHours();
		let removedHours = 24 - currentHour;


		for (let i = removedHours; i < data.length; i++){
			let fast_price = data[i].min;
			let data_time = new Date(data[i].time);


			
			
			let dayDifference = currentTime.getDay() - data_time.getDay();
			let tableColumnIndex = (dayDifference + 7) % 7;
			console.log(tableColumnIndex);
			if (tableColumnIndex >= 0 && tableColumnIndex < 7) {
				tableColumnIndex = 6 - tableColumnIndex;
				r[data_time.getHours()][tableColumnIndex] = Math.floor(fast_price / 1e9);
			}
			

			
			
		}

	}

	function get_hourlyData() {
		var oReq = new XMLHttpRequest();
		oReq.addEventListener("load", hourlyGasListener);
		oReq.open("GET", "https://www.gasnow.org/api/v3/block/history?filter=y&zoom=w&utm_source=web");
		oReq.send();
	}

	window.setTimeout(get_hourlyData, 100)


	let gas_used = 100000;
	let gas_price = 0;
	let ethpriceinput = 0;

	function handleGasClick(val) {
		gas_price = val;
	}

	function handlePrice() {
		console.log("aaaa");
		ethpriceinput = ethprice;
	}

	export let name;
</script>

